/** ==============================
 *  Main container styles.
 *  =============================*/
.main {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	background: -webkit-gradient(linear, left top, right top, color-stop(5%, rgb(230, 230, 230)), color-stop(10%, rgb(242, 242, 242)), color-stop(55%, rgb(255, 255, 255)), color-stop(95%, rgb(242, 242, 242)), to(rgb(230, 230, 230)));
	background: -webkit-linear-gradient(left, rgb(230, 230, 230) 5%, rgb(242, 242, 242) 10%, rgb(255, 255, 255) 55%, rgb(242, 242, 242) 95%, rgb(230, 230, 230) 100%);
	background: -o-linear-gradient(left, rgb(230, 230, 230) 5%, rgb(242, 242, 242) 10%, rgb(255, 255, 255) 55%, rgb(242, 242, 242) 95%, rgb(230, 230, 230) 100%);
	background: linear-gradient(90deg, rgb(230, 230, 230) 5%, rgb(242, 242, 242) 10%, rgb(255, 255, 255) 55%, rgb(242, 242, 242) 95%, rgb(230, 230, 230) 100%);
}

.main a {
	position: relative;
	text-decoration: none;
	color: #006080;
	-webkit-transition: color 0.25s linear;
	-o-transition: color 0.25s linear;
	transition: color 0.25s linear;
}

.main a:hover, .main a:focus {
	color: #0099cc;
}

.main a::after {
	border-bottom: 1px solid #0099cc;
}

/* Home page top styles */
.home_top {
	background-image: url(../images/home_bg.jpg);
	background-size: cover;
	background-position: center;
	width: 100%;
	min-height: 50vh;
	border-bottom: 1px solid grey;
	-webkit-box-shadow: 1px 2px 10px 1px grey;
	        box-shadow: 1px 2px 10px 1px grey;
}

.home_top_container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-align: center;
	    align-items: center;
	max-width: 980px;
	margin: 0 auto;
}

.home_top_content {
	margin: 1em 1em 0;
	padding: 1em 0;
	max-width: 40vw;
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.2)), color-stop(10%, rgba(255, 255, 255, 0.5)), color-stop(rgba(255, 255, 255, 0.7)), color-stop(90%, rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.2)));
	background: -webkit-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: -o-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	border-radius: 26px;
	color: black;
	border: 1px solid deepskyblue;
	border-color: transparent lightyellow lightyellow transparent;
	-webkit-box-shadow: 4px 4px 15px 1px lightblue;
	        box-shadow: 4px 4px 15px 1px lightblue;
}

.home_top_content > * {
	/* Let 1em margin space to all home_top_content children */
	margin: 0 1em;
}

.home_top_content:last-child {
	margin-bottom: 1em;
}

/* Create some visual bullets on the bottom of the top background */
.home_top_bullets {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-align: center;
	    align-items: center;
}

.home_top_bullet {
	padding: 1em;
	margin: 1em;
	border-radius: 100%;
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.2)), color-stop(10%, rgba(255, 255, 255, 0.5)), color-stop(rgba(255, 255, 255, 0.7)), color-stop(90%, rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.2)));
	background: -webkit-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: -o-linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5) 10%, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5) 90%, rgba(255, 255, 255, 0.2) 100%);
	border: 1px solid deepskyblue;
	border-color: transparent lightyellow lightyellow transparent;
	-webkit-box-shadow: 4px 4px 15px 1px lightblue;
	        box-shadow: 4px 4px 15px 1px lightblue;
}



